/**
 * 
 */
(function() {
    'use strict';

    function init() {
        const pageTopBtn = document.querySelector('.page-top-btn');

        if (!pageTopBtn) {
            return;
        }

        pageTopBtn.addEventListener('click', function(e) {
            e.preventDefault();

            // 
            window.scrollTo({
                top: 0,
                behavior: 'smooth'
            });
        });
    }

    // DOM
    if (document.readyState === 'loading') {
        document.addEventListener('DOMContentLoaded', init);
    } else {
        init();
    }
})();
